.specialties-container {
    padding: 5% 10%;
    display: flex;
    flex-direction: column;
    text-align: left;
    svg {
        color: white;
    }

    h3 {
        font-size: 1em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        color: #26B5A4;
        letter-spacing: -0.04em;
        font-weight: medium;
    }
}

.specialties-grid {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Two equal columns */
    gap: 30px; /* Space between columns */
    align-items: center; /* Center vertically */
  }

.lottie-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.specialties-list {
    margin-top: 20px; /* Spacing between the title and the list */
    display: flex;
    flex-direction: column;
    gap: 10px; /* Adjust this value to reduce spacing between items */
  }
  
  .specialties-item {
    display: flex;
    align-items: center; /* Align icon and text vertically */
    flex-direction: row; /* Icon and text are side by side */
    margin-bottom: 0px; /* Remove extra margins to minimize spacing */
    gap: 0px; /* Space between the icon and text */
  }
  
  .bullet-point {
    display: flex;
    align-items: center;
  }

  
  .bullet-point p {
    margin: 5px 0 0;
    font-size: 1rem;
  }
  .bullet-point {
    display: flex;
    align-items: center; /* Align icon and text vertically */
    gap: 16px; /* Add spacing between the icon and the text */
  }
  
  .specialties-list {
    margin-top: 24px; /* Optional: Add spacing between the title and the list */
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .specialties-item p {
    margin: 0; /* Remove default margins */
    font-size: 1rem; /* Adjust font size if necessary */
    text-align: left; /* Ensure text remains left-aligned */
  }

  @media screen {
    @media (max-width: 768px) {
        .specialties-container {
            display: block;
            padding-left: 5%;
            padding-right: 5%;
        }
    
        .specialties-container .specialties-grid {
            display: initial;
        }
        
        .specialties-container .specialties-grid .specialties-item {
            padding-bottom: 5%;
        }
    }

    @media (max-width: 567px) {
        .skills {
            font-size: 12px;
        }
    }
}