.dark-mode .about-section {
    background-color: #051312;
}

.about-section {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-areas: 
        "image content lottie";
    gap: 30px;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 0px 10%;
    min-height: 700px;
    background-image: url("../images/dark-mode_bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
}

.about-section .content {
    grid-area: content;
    text-align: left;
    z-index: 2;

    .social_icons {
        display: flex;
        flex-direction: row;
        gap: 10px;
        color: white;

        a {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: #D63B9B;
            transition: all 0.3s ease-in-out;
            
            &:hover{
                background-color: #26B5A4;
                color: white;
            }
            }

        svg {
            font-size: 1.8em;
        }
    }

    .mobile_social_icons {
        display: none;
    }

    p {
        font-size: 1.5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        color: #E689C3;
    }

    h1 {
        font-size: 5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
        color: #26B5A4;
        letter-spacing: -0.04em;

        @media screen and (max-width: 1786px) {
            font-size: 4em; /* Slightly smaller for medium-sized screens */
          }
      
          @media screen and (max-width: 1280px) {
            font-size: 3.5em; /* Smaller for smaller desktops */
          }
      
          @media screen and (max-width: 768px) {
            font-size: 3em; /* Mobile-friendly size */
            text-align: center; /* Center-align on smaller screens */
          }
      
          @media screen and (max-width: 480px) {
            font-size: 2.5em; /* Extra small for very small screens */
          }
    }
}
.about-section .lottie-wrapper {
    grid-area: lottie;
    flex: 1; /* Allow the Lottie animation to take up equal space as content */
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Adjust based on your design needs */
    max-width: 400px; /* Limit the maximum width of the animation */
        @media screen and (min-width: 768px) {
            width: 400px; /* Medium screens */
            height: 400px;
        }
        
        @media screen and (min-width: 1200px) {
            width: 500px; /* Larger screens */
            height: 500px;
        }
        
        @media screen and (min-width: 1786px) {
            width: 600px; /* Extra large screens */
            height: 600px;
        }
  }
  
.about-section .image-wrapper {
    grid-area: image;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
        width: 300px;
        border-radius: 50%;
    }
}

.about p {
    text-align: center;
    padding-top: 30%;
    padding-bottom: 30%;
}

@media screen {
    @media (max-width: 768px) {
        .nav-left{
            padding-top: 20px;
            float:left;
        }

        .nav-left img {
            height: 35px
        }

        .nav-right {
            float:right;
        }

        .nav-right li {
            padding: 10px 15px;
            list-style:none;
            font-size:13px;
            color:white;
            width: 10%;
        }

        .nav-right ul {
            list-style-type: none;
            margin: 0px;
            padding: 0px;
        }

        body {
            width: 100%;
        }

        .about {
            padding-left: 5%;
            padding-right: 5%;
            font-size: 12px;
        }
    }

    @media (max-width:768px) {
        .about-section h1 {
            font-size: 4em !important;
            text-align: left;
        }

        .about-section p {
            text-align: left;
        }

        .container {
            display: initial;
        }

        .social_icons {
            display: none !important;
        }

        .mobile_social_icons {
            padding-top: 10px;
            display: flex !important;
            flex-direction: row;
            gap: 10px;
            color: white;
    
            svg {
                font-size: 1.8em;
            }
        }

        .about-section {
            padding-top: 150px;
            display: block;
            padding-left: 0px;
            padding-right: 0px;
        }

        .about-section .content {
            width: 90%;
            padding-left: 5%;
            padding-right: 5%;
            padding-bottom: 5%;
        }

        .about-section .image-wrapper {
            padding-left: 5%;
            width: 100%;
        }
    }
}